* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url("https://images.unsplash.com/photo-1622099330140-69125ac0d6a9?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8bGlmZSUyMHF1b3RlfGVufDB8fDB8fA%3D%3D&w=1000&q=80");
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-size: "cover";
  height: 100vh;
  margin-top: -70px;
  font-size: 27px;
}

#quote-box {
  background-color: rgba(12, 12, 12, 0.393);
  width: 550px;
  margin: 10rem auto;
  padding: 3rem;
  border-radius: 70px;
  min-width: 350px;
  box-shadow: inset -7px 0 19px -7px rgba(5, 5, 5, 0.351),
    -26px 12px 33px -8px rgba(10, 10, 10, 0.36);
  display: flex;
  flex-direction: column;
  align-items: left;
}

.fa {
  color: rgba(255, 255, 255, 0.758);
  font-size: 77px;
  padding: 40px 0 0 40px;
}

#text {
  font-size: 1.7rem;
  font-family: cursive;
  margin-bottom: 1.5rem;
  color: rgba(255, 255, 255, 0.821);
  text-transform: uppercase;
  padding: 0 30px 0 30px;
  font-weight: 900;
}

#author {
  color: rgba(255, 255, 255, 0.758);
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
  text-align: right;
  font-family: serif;
}

#buttons {
  display: flex;
  align-items: center;
  margin-top: 2rem;
}

.button {
  display: flex;
}

.button>a {
  margin-right: .5rem;
  background: #0059ff00;
  padding: .3rem .5rem;
  cursor: pointer;
  border-radius: 10px;
}

.button>a:hover {
  opacity: 0.9;
}

#new-quote {
  background: #002ead00;
  border: none;
  color: rgba(255, 255, 255, 0.785);
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border-radius: 7px;
  box-shadow: 0px 0px 2px 2px rgba(91, 91, 162, 0);
  outline: none;
  cursor: pointer;
}

#new-quote:hover {
  background-color: #5269a7;
  transition: 0.7s;
  opacity: 0.9;
}

#tweet-quote {
  background: #002ead00;
  border: none;
  color: rgba(255, 255, 255, 0.801);
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border-radius: 7px;
  box-shadow: 0px 0px 2px 2px rgba(94, 98, 170, 0);
  outline: none;
  cursor: pointer;
}

#tweet-quote:hover {
  background-color: #6273a2;
  transition: 0.7s;
  opacity: 0.9;
}

.footer {
  width: 450px;
  text-align: center;
  display: block;
  margin: 15px auto 30px auto;
  font-size: 0.7em;
  color: rgba(250, 235, 215, 0.785);
}
